.button-blue {
    color: #fff;
    border: 2px solid #2d6cc5;
    background-color: #398cff;

    .fa-spinner {
        animation-name: spin;
        animation-duration: 5000ms;
        animation-iteration-count: infinite;
        animation-timing-function: linear;

        &:before {
            font-size: 0.9em;
        }
    }

    &:hover {
        background-color: #398cff;
    }

    &:active {
        background-color: #2d6cc5;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.app-title-bar {
    padding: 21px;
    background-color: #006c91;

    span {
        display: block;
    }

    &__content {
        color: #fff;

        &__title {
            margin-bottom: 8px;
            font-size: 1.3em;
            font-weight: 600;
        }

        &__author {
            font-size: 1em;
        }
    }
}

.nav-bar {
    a {
        font-size: 1em;
        font-weight: 700;
        letter-spacing: 1.8px;
        text-transform: uppercase;
        text-decoration: none;
        color: #1e375a;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;

        &:hover {
            color: #2364d2;
        }
    }
}

@mixin form-error-msg {
    border: 3px solid red;
    background-color: #ffe0e0;
    color: red;
    padding: 10px;
    font-size: 1em;
    font-weight: 600;
    text-align: left;
    line-height: 1.3em;
    margin-bottom: 20px;
}

@mixin form-nav {
    font-size: 1.2em;
    margin-bottom: 20px;
    font-weight: 600;
}

@mixin form-nav-links {
    text-decoration: none;
    color: #398cff;
}

@mixin form-nav-links-hover {
    color: #2364d2;
}

.site-warning-section {
    padding: 20px;
    background-color: #ddd;

    &__content-wrapper {
        width: 830px;
        margin: 0 auto;
        padding: 30px;
        border: 5px solid #abab07;
        background-color: #525228;
        text-align: left;
        color: yellow;
        position: relative;
    }
    &__close {
        padding: 5px 10px;
        background-color: #abab07;
        color: #fff;
        font-size: 30px;
        position: absolute;
        top: -5px;
        right: -5px;
        &:hover {
            color: #ddd;
        }
    }
}

.allinone-auth {
    width: 500px;
    margin: 0 auto;
    margin-top: 90px;
    display: block;

    .login-reg-form {
        &__component-error {
            @include form-error-msg;
        }

        &__header {
            text-align: left;

            &__title {
                margin-bottom: 10px;
                font-size: 1.6em;
                font-weight: 600;
            }

            &__description {
                margin-bottom: 20px;
                font-size: 1.1em;
                font-weight: 500;
                line-height: 1.4em;
                color: #1e1e1e;
            }
        }

        &__general-message {
            @include form-error-msg;
        }

        &__form {
            margin-bottom: 35px;

            &__input-wrapper {
                position: relative;
                height: 43px;
                margin-bottom: 25px;
            }

            &__input {
                width: 100%;
                padding: 9px 0px 9px 37px;
                font-size: 1.1em;

                &.invalid {
                    border-color: #ff0000;
                    background-color: #ffecec;
                }
            }

            &__input-icon {
                position: absolute;
                bottom: 15px;
                left: 13px;
                font-size: 1em;
            }

            &__input-icon-eye {
                position: absolute;
                bottom: 15px;
                right: 13px;
                font-size: 1em;
                cursor: pointer;
            }

            &__validation-msg {
                position: absolute;
                bottom: -17px;
                left: 0px;
                font-size: 0.8em;
                color: red;
            }

            // &__button-wrapper {}
            &__submit {
                width: 100%;
                padding: 9px;
                font-size: 1.1em;
            }

            &__reset-password,
            &__back-to-login,
            &__create-account {
                @include form-nav;

                a {
                    @include form-nav-links;

                    &:hover {
                        @include form-nav-links-hover;
                    }
                }
            }
        }
    }
}

.home-logged-in-message {
    margin: 0 auto;
    margin-top: 90px;
    h2 {
        font-size: 2em;
        font-weight: 600;
        margin-bottom: 30px;
    }

    p {
        @include form-nav;

        a {
            @include form-nav-links;
            &:hover {
                @include form-nav-links-hover;
            }
        }
    }
}

$tablewidth: 1100px;

.account {
    &__nav-bar {
        &__nav {
            width: $tablewidth;
            margin: 0 auto;
            margin-top: 20px;
            text-align: right;
            a {
                @include form-nav-links;
                font-size: 1.2em;
                font-weight: 600;
                text-transform: uppercase;
                &:hover {
                    @include form-nav-links-hover;
                }
            }
        }
    }
}

.api-key-table {
    width: $tablewidth;
    margin: 0 auto;
    margin-top: 70px;
    table {
        width: 100%;
    }
    table,
    th,
    td {
        border: 1px solid black;
        border-collapse: collapse;
    }
    th,
    td {
        border-color: black;
    }
    tr th {
        background-color: #575757;
        color: white;
        font-size: 1em;
        font-weight: 700;
        border-color: #1c1c1c;
    }
    th {
        padding: 10px;
    }
    td {
        padding: 15px 10px;
        font-size: 0.9em;
    }
    &-section {
        padding: 0 0 100px 0;
    }
    &__toolbar {
        margin-bottom: 20px;
        text-align: left;
        &__generate {
            width: 180px;
            margin-right: 40px;
            padding: 5px 0;
            font-size: 1em;
            font-weight: 600;
            cursor: pointer;
        }
        &__api-docs-path {
            @include form-nav-links;
            font-size: 1.2em;
            font-weight: 600;
            text-transform: uppercase;
            &:hover {
                @include form-nav-links-hover;
            }
        }
    }
    &__table-wrapper {
        border-collapse: collapse;
        border: 2;
    }
    &__header {
        &__column {
            &__apikeycreated {
                width: 17%;
            }
            &__apikey {
            }
            &__actions {
                width: 14%;
            }
        }
    }
    &__body {
        &__row {
            &__column {
                &__apikeycreated {
                }
                &__apikey {
                    &.apikey-show-true {
                        .apikey-show-true {
                            display: block;
                        }
                        .apikey-show-false {
                            display: none;
                        }
                    }
                    &.apikey-show-false {
                        .apikey-show-true {
                            display: none;
                        }
                        .apikey-show-false {
                            display: block;
                        }
                    }
                    &__asterisk {
                        width: 470px;
                        display: inline-block;
                        text-align: left;
                    }
                    &__hint {
                        letter-spacing: 2px;
                        margin-left: 4px;
                        display: inline-block;
                        width: 60px;
                        text-align: left;
                    }
                }
                &__actions {
                    a {
                        display: inline-block;
                        margin: 0 9px;
                        cursor: pointer;
                    }
                    &__copy {
                        &:active .fa-copy {
                            color: #8b8b8b;
                        }
                    }
                    .delete-link {
                        position: relative;
                        .fa-solid {
                            color: red;
                        }
                        .confirm-wrapper {
                            z-index: 10;
                            position: absolute;
                            display: flex;
                            flex-flow: column;
                            align-items: end;
                            top: 0;
                            right: 0;
                            &__icon {
                                width: 30px;
                                height: 25px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background-color: white;
                                border-top: 2px solid black;
                                border-left: 2px solid black;
                                border-right: 2px solid black;
                                .fa-solid {
                                    color: black;
                                }
                            }
                            &__actions {
                                padding: 15px 10px;
                                display: flex;
                                background-color: white;
                                border: 2px solid black;
                                position: relative;
                                &__placeholder {
                                    width: 26px;
                                    height: 2px;
                                    position: absolute;
                                    top: -2px;
                                    right: 0;
                                    background-color: #fff;
                                }
                                button {
                                    margin: 0 10px;
                                    padding: 6px 17px;
                                    font-size: 1em;
                                    font-weight: 600;
                                    cursor: pointer;
                                }
                                &__cancel {
                                    color: #3c3c3c;
                                    background-color: #d7d7d7;
                                    border: 2px solid #8d8d8d;
                                }
                                &__confirm {
                                    width: 93px;
                                    color: #fff;
                                    text-align: center;
                                    background-color: red;
                                    border: 2px solid #cb0000;
                                    .fa-solid {
                                        color: white;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
